import React from "react";
import { isTagShop, isTagbox } from "../../../../actions/api";
import Image from '../../Elements/Media/Image';
import Video from '../../Elements/Media/Video';
import MediaIcon from '../../Elements/MediaIcon/MediaIcon';

const Media = ({ Post, CardStyle, ThemeStyle, ProductSetting, index, inView }) => {
  const size =  isTagbox ? CardStyle.style.ratio ? CardStyle.style.ratio : 100 : 100
  const autoPlay = [3,5].includes(Post.type) && ThemeStyle.autoPlay == 1 && inView ? true : false
  const mediaIcon = isTagShop && Post.hotspot ? false : true
  return <div className="tb_hs_media_wrap">
    <MediaIcon Post={Post} IconClass="tb_hs_media_icon" show={mediaIcon} hideVideo={ThemeStyle.autoPlay} /> 
    {autoPlay ?
        <Video key={Post.id} VideoClass="tb_hs_video" Post={Post} size={size} isCover={true} muted={true} autoPlay={true} index={index} inView={inView} />
          :
        <Image key={Post.id} ImageClass="tb_hs_image" Post={Post} size={size} ProductSetting={ProductSetting} hotspot={true} blurBG={isTagShop} index={index} inView={inView} />
    }
  </div>
}
export default Media