import React, { createRef, PureComponent, Suspense } from "react";
import { connect } from "react-redux";
import { Splide } from '@splidejs/react-splide';
import '../scss/ThemesSplide.scss'
import './scss/HorizontalSlider.scss'
import { isSocialEmbed, isSocialWall, isTagShop } from "../../../actions/api"
import { getColumns } from '../../../utils'
import { nextData } from "../../../actions/moredataAction";
import Card from "./Elements/Card";

class HorizontalSlider extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      parent_id: '',
      postData: [], 
      loadData: false,
      resizeKey: Math.random(), // Add a resize key
    }
    this.inputRefs = {}
    this.isDragging = false; // Class property to track dragging
    this.splideRef = createRef(); // Create a ref for Splide
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole && classNameRole.forEach((element) => {
        element.removeAttribute("aria-hidden");
        element.removeAttribute("role");
      });
    }, 100);

    const { Post, loadBranding } = this.props;
    this.setState({ Post });
    window.addEventListener('keydown', this.handleKeyPress);
    window.addEventListener('resize', this.handleResize); // Add resize listener
    loadBranding();
  }

  handleKeyPress = event => {
    try {
      const { ThemeStyle } = this.props
      if (ThemeStyle.slide.autoSlide == 1) {
        const isInput = ['INPUT', 'TEXTAREA'].includes(event.target.tagName);
        if (!isInput && event.code === 'Space') {
          const { parentID } = this.props
          event.preventDefault(); // Prevent scrolling the page
          var Autoplay = this.inputRefs[parentID].splide.Components.Autoplay;
          if (Autoplay.isPaused()) Autoplay.play();
          else Autoplay.pause();
        }
      }
    }
    catch (error) {
    }
  }
  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress);
    window.removeEventListener('resize', this.handleResize); // Clean up resize listener
  }
  requestData = () => {
    const { appendData, preRender, hasMoreData, nextData, ThemeStyle, ThemeInfo } = this.props;
    !this.state.loadData && this.updatepost(0)

    if (hasMoreData) {
      nextData(
        isTagShop ? ThemeInfo.webId : ThemeInfo.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle.totalPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  autoScrollSlider = (splide) => {
    const { ThemeStyle } = this.props
    const autoScrollStatus = ThemeStyle.autoScrollStatus;

    splide.on("drag", () => {
      this.isDragging = true; // Reset flag after move
      !this.state.loadData && this.updatepost(0)
    });

    splide.on("move", () => { !this.state.loadData && !this.isDragging && this.updatepost(3) });

    if (autoScrollStatus == 1 || isTagShop) {
      const currentIndex = splide.index
      const slideLength = splide.length
      const permove = splide["_o"].perPage
      if (slideLength - currentIndex === permove) this.requestData()
    }
  }

  updatepost = (e) => {
    if (!this.state.loadData) {
      this.setState({ loadData: true })
      setTimeout(() => this.props.loadMoredata(), (e * 1000))
    }
  }
  handleResize = () => {
    this.setState({ resizeKey: Math.random() }); // Force re-render
    if (this.splideRef.current) {
      this.splideRef.current.refresh(); // Refresh Splide instance
    }
  };
  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, onClickToCTA, parentID, loaderData, onClickPopUpSlider, handlePressStart, handlePressEnd, containerSize } = this.props;
    const { resizeKey } = this.state
    const inputName = parentID;
    var newPostdata = []
    const slidePost = ThemeStyle.slide.autoSlide;
    const slideDuration = ThemeStyle.slide.slideDuration;
    const rowCount = !ThemeStyle.row || ThemeStyle.row === 0 ? 1 : (postData.length < 15 && (isSocialWall || isSocialEmbed)) ? 1 : ThemeStyle.row;
    const minCardSize = 310;
    const responsiveColumns = Math.max((containerSize.width / minCardSize).toFixed(), 1)
    const columns =  getColumns(containerSize.width, ThemeStyle.column, responsiveColumns);


    var i = 0, toalPost = postData.length;
    while (i < toalPost) { newPostdata.push(postData.slice(i, i += rowCount)) }

    const isCenter = columns >= toalPost ? true : false
    const settings = {
      clones: isCenter ? 0 : 1,
      type: 'slide',
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: columns,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      updateOnMove: true,
      dragThreshold: 1000,
      pagination: false,
      arrows: isCenter ? false : true
    }
    return (parentID != "" ? <div className="tb_hs_post_container">
      {newPostdata.length ? <Splide
        key={resizeKey} // Force re-render on resize
        ref={(el) => {
          this.splideRef = el;
          this.inputRefs[inputName] = el;
        }}
        hasTrack={false} className={`tb_hs_post_slider ${isCenter ? 'tb_hs_slider_center' : ''}`} options={settings} onMove={this.autoScrollSlider}>
        <div className="splide__track">
          <div className="splide__list">
            {(ThemeStyle.row > 1 && newPostdata.length !== 1 && newPostdata.length % 2 !== 0 ? newPostdata.pop() : newPostdata) && ((newPostdata && newPostdata.length > 0)) && newPostdata.map((item, index) => {
                return <div key={`tb_${index}_${index}`} className={`splide__slide splide__${index}`}>
                    {item.map((subItem, childIndex) => {
                      const cardData = completeDataObject[subItem];
                      return <div className="tb_hs_col_wrap"
                        data-id={cardData.id}
                        key={childIndex}
                        onMouseDown={handlePressStart}
                        onMouseUp={(e) => handlePressEnd(e, index, cardData)}
                        onTouchStart={handlePressStart}
                        onTouchEnd={(e) => handlePressEnd(e, index, cardData)}>
                        <Card Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} postSizeInPx={containerSize.width / columns} /></div>
                    })
                    }
                </div>
              })}
          </div>
        </div>
        <div className="splide__arrows splide__arrows--ltr tb_hs_arrow_wrapper_">
          <div className="tb_hs_arrow splide__arrow splide__arrow--prev tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide" ><div></div></div>
          <div className={`tb_hs_arrow splide__arrow splide__arrow--next  tb_hs_arrow_right__ tb__icon tb-arrow-right-alt ${loaderData.isShowMoreLoading ? `tb_a_spinner` : ``}`} onClick={isTagShop ? event => this.requestData() : null} role="button" aria-label="Go to next slide" ><div></div></div>
        </div>
      </Splide> : null}
    </div>: null)
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    nextData: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HorizontalSlider);