import React, { useEffect } from 'react'
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import { PostTrimContent } from '../../../../utils'
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
import { isTagShop, isTagbox } from "../../../../actions/api";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover"

const trim_content_length =200;
const Card = ({onClickPopUpSlider, postSizeInPx, index, ThemeStyle, CardStyle, Post, ProductSetting, ThemeInfo }) => {

  const { networkId } = Post
  const { style } = CardStyle

  const mediaType = Post.type !== 1 ? true : false;
  const postTextContent = CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text
  const responsiveCardSize2x = postSizeInPx < 200 ? true : false
  const ctaStatus = !CardStyle.content.status;
  const isContent = Post.isShopIcon ? ctaStatus == 0 ? true : false : CardStyle.content.status == 1 ? false : true
  const shopBtn = Post.isShopIcon ? ctaStatus == 0 ? false : true : false
  const themeMode = (CardStyle.style.mode == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_hs_color_overlay' : 'tb_hs_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? CardStyle.style.background : '' }

  useEffect(() => {
    if (Post.isPopUp) onClickPopUpSlider(index, Post)
  }, [index, Post.isPopUp, Post]);
  return  <div id={`tb_hs_id-${Post.id}`} className={`tb_hs_post_wrapper${responsiveCardSize2x ? ' tb_hs_post_wrapper2x' : ''}`} style={{padding: style.padding / 2, opacity: 1, transition:  'opacity .5s ease-in-out' }} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} area-label={Post.content.text} role="article">
          <div id={Post.id} className={`tb_hs_post_in${isTagShop ? ' tb_hs_post_ani' : ''}`} role="button" aria-label="Popup"> 
              {mediaType ? <Media key={index} Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} index={index} inView={true} /> : ''}
              {!Post.isHotspot ? <>
                <div className="tb_hs_post_details">
                  <div className="tb_hs_contant_wrapper">
                    {isTagShop ? <div className="tb_hs_social_">
                      <Network networkClass={'tb_hs_social__ico'} networkId={networkId} iconStyle={CardStyle.icon} />
                    </div> : null}
                    {shopBtn ? <ShopButton CardStyle={CardStyle} btnClass={'tb_hs_btn_wrap'} /> : null}
                    {(isContent || Post.type == 1) ? 
                    <PostContent contentClass={`tb_hs_content`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.content.title} /> 
                    : null}
                  </div>
                  {!isTagShop ?
                    <AuthorDetails Post={Post} CardStyle={CardStyle} />
                    : null}
                  {Post.share.status ? <ShareOnHover Post={Post} ThemeInfo={ThemeInfo} shareClass={`tb_hs_share_container`} /> : null}
                </div>
                {!Post.isHotspot ? <div className={`tb_hs_post_overlay ${overlay}`} style={overlayStyle}><div></div></div> : null}
              </> : null}
          </div>
        </div>
          
}
export default Card